.resovePage {
  background: #fff;
}

.resovePage .content {
  padding: 0 20px;
}

.resovePage * {
  box-sizing: border-box;
}

.banner {
  position: relative;
  z-index: 3;
  min-height: 160px;
}

.banner::after {
  position: absolute;
  z-index: -1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: '';
  background-image: url('@/static/images/product/banner.jpg');
  background-size: cover;
}

.banner .bannerContent {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
  display: flex;
  flex-direction: column;
  justify-content: center;
  background-color: rgba(0, 0, 0, 0.5);
  width: 100%;
  height: 100%;
  padding-top: 30px;
  text-align: center;
}

.banner .bannerContent h4 {
  font-size: 30px;
  line-height: 44px;
  margin: 0;
  margin-bottom: 15px;
  padding: 0;
  color: #ffffff;
}

.banner .bannerContent p {
  font-size: 12px;
  margin: 0;
  padding: 0;
  text-transform: uppercase;
  color: #ffffff;
}

.board {
  margin: 35px 0 0 0;
}

.board .content {
  display: flex;
  flex-wrap: wrap;
  margin: 35px 0 0 0;
  padding: 0;
  border-top: 1px solid #e7f3f9;
}

.hairlines .board .content {
  border-top: 0.5px solid #e7f3f9;
}

.board .content .boardItem {
  width: 50%;
  min-height: 260px;
  padding: 20px;
  border-bottom: 1px solid #e7f3f9;
}

.hairlines .board .content .boardItem {
  border-bottom: 0.5px solid #e7f3f9;
}

.board .content .boardItem.boardLeftBorder {
  border-left: 1px solid #e7f3f9;
}

.hairlines .board .content .boardItem.boardLeftBorder {
  border-left: 0.5px solid #e7f3f9;
}

.board .content .boardItem h4 {
  font-size: 17px;
  margin: 0 0 10px 0;
  padding: 0;
  color: #333333;
}

.board .content .boardItem .childrenItem {
  display: flex;
  margin-bottom: 13px;
}

.board .content .boardItem .childrenItem .childrenIcon {
  width: 12px;
  height: 12px;
  margin: 5px 8px 0 0;
}

.board .content .boardItem .childrenItem .childrenDesc {
  font-size: 14px;
  flex: 1;
  word-break: break-all;
  color: #333;
}

.quality {
  margin: 35px 0 0 0;
  padding-bottom: 30px;
}

.quality .content {
  margin: 35px 0 0;
}

.quality .content .qualityItem {
  margin-bottom: 10px;
}

.quality .content .qualityItem .qualityHeader {
  padding: 20px;
  border-radius: 4px 4px 0 0;
  color: #333;
}

.quality .content .qualityItem .qualityTitle {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.quality .content .qualityItem .qualityTitle .qualityIcon {
  width: 4px;
  height: 13px;
  margin-right: 8px;
  background: #666;
}

.quality .content .qualityItem .qualityTitle .qualityName {
  font-size: 17px;
  line-height: 13px;
  color: #666666;
}

.quality .content .qualityItem .qualityDesc {
  font-size: 16px;
  color: #666666;
}

.quality .content .qualityImage {
  min-height: 120px;
}

.quality .content .qualityImage img {
  width: 100%;
}

.advantage {
  padding: 50px 0 35px;
  background: #f3f8fe;
}

.advantage .content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: 35px;
}

.advantage .content .advantageItem {
  width: 47%;
  min-height: 260px;
  margin-bottom: 15px;
  padding: 20px 15px;
  border-radius: 4px;
  background: #ffffff;
}

.advantage .content .advantageItem .advantageImage {
  margin: 0 auto 14px;
}

.advantage .content .advantageItem .advantageImage img {
  width: 100%;
}

.advantage .content .advantageItem .advantageTitle {
  margin-bottom: 10px;
  font-size: 17px;
  line-height: 24px;
  height: 24px;
  text-align: center;
  color: #333333;
}

.advantage .content .advantageItem .advantageSubTitle {
  margin-bottom: 20px;
}

.advantage .content .advantageItem .advantageDesc {
  font-size: 14px;
  word-break: break-all;
  color: #666666;
}