.header .title {
  display: flex;
  align-items: center;
  margin-top: 0;
  margin-bottom: 10px;
}

.header .title .icon {
  width: 5px;
  height: 22px;
  background: #333;
}

.header .title .name {
  font-size: 24px;
  line-height: 22px;
  padding: 0 20px 0 15px;
}

.header .desc {
  font-size: 16px;
  padding: 0 20px;
  text-transform: uppercase;
  opacity: 0.75;
  color: #333333;
}